<template>
    <div class="switch-button-container" :class="classes">
        <label class="switch-label" v-if="label">{{ label }}</label>
        <input type="checkbox" :name="name" :checked="checked" hidden/>
        <div class="switch-button" 
            role="switch"
            :class="{'is-checked': checked, 'is-disabled': disabled}"
            :style="{backgroundColor: checkedColors[Number(checked)]}"
            @click="switchChangeHandler" >
            <span v-if="showTextBool">{{ checkedTextValues[Number(checked)] }}</span>
            <span v-else><!--  --></span>
        </div>
    </div>
</template>
<script>
export default {
    name: 'SwitchButton',
    props: {
        value: {
            type: [Boolean, String, Number],
            default: false
        },
        name: {
            type: String,
            default: null
        },
        label: {
            type: String,
            default: null
        },
        classes: {
            type: String,
            default: null
        },
        inline: {
            type: Boolean,
            default: false
        },
        disabled: {
            type: Boolean,
            default: false
        },
        checkedText: {
            type: Array,
            default: () => []
        },
        showText: {
            type: Boolean,
            default: false
        },
        checkedColors: {
            type: Array,
            default: () => []
        }
    },
    data () {
        return  {
            // checked: false
        }
    },
    computed: {
        checked: {
            get() {
                return Boolean(this.value)
            },
            set(value) {
                if(this.disabled) return
                this.$emit('input', value)
            }
        },
        showTextBool() {
            return this.showText
        },
        checkedTextValues() {
            if(this.checkedText.length > 0) {
                return this.checkedText
            }else {
                return ["Close", "Open"]
            }
        }
    },
    methods: {
        switchChangeHandler() {
            if(this.disabled) return 
            this.$emit('input', !this.checked)
            this.$emit('change', !this.checked)
        }
    }
}
</script>
<style scoped>
.switch-button-container {
    display: flex;
    margin: 0 5px;
}
.switch-button-container .switch-label {
    line-height: 20px;
    padding: 0 10px;
    font-size: 14px;
}
.switch-button-container .switch-button {
    position: relative;
    min-width: 10px;
    height: 20px;
    color: #ffffff;
    font-size: 12px;
    line-height: 20px;
    padding: 0px 8px 0px 15px;
    background-color: red;
    border-radius: 10px;
    box-shadow: inset 0 0 3px #e0e0e0;
    cursor: pointer;
    transition: all 0.5s linear;
}
.switch-button-container .switch-button.is-disabled{
    filter: contrast(0.6);
    cursor: not-allowed;
}
.switch-button-container .switch-button::after {
    content: " ";
    position: absolute;
    left: -5px;
    top: 0px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #ffffff;
    box-shadow: 0 0 3px #a9a9a9;
    transition: all 0.5s linear;
}
.switch-button-container .switch-button.is-checked {
    background-color: green;  
    padding: 0px 15px 0px 8px; 
    transition: all 0.5s linear;
}
.switch-button-container .switch-button.is-checked::after {
    left: calc(100% - 15px);
    transition: all 0.5s linear;
} 
</style>